  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:rgb(239, 239, 193);
  display: flex;
  /* align-items: center; */
  padding-top: 100px;
  justify-content: center;
  height: 100vh;
}

